module.exports = "\"use effects\";\ntry {\n  await work();\n} handle \"log\" with ({ message }) {\n  console.log(message);\n  recall\n}\n\nasync function work() {\n  const res = await fetch();\n  perform {\n    type: \"log\",\n    message: `fetch completed with status: ${res.status}`,\n  };\n  return res.json();\n}\n"